/**
 * @prettier
 */

import { Button, TextField, Typography } from '@material-ui/core';
import { useEffect, useRef, useState } from 'react';
import { useNavigate, useParams } from 'react-router';
import { useRecoilValue } from 'recoil';

import { useModelUpdate } from '../../../hooks/ModelUpdate';
import { GlobalUser } from '../../../stores/User';
import { useModelByPkQuery } from '../../../utils/graphql/generated';
import useStyles from './style';

export type UpdateFormProps = {
  modelFile: File | undefined;
  thumbFile: File | undefined;
};

export const UpdateForm = ({ modelFile, thumbFile }: UpdateFormProps) => {
  const styles = useStyles();

  // URLID
  const { objId } = useParams();
  // ID
  const { data: currentModel } = useModelByPkQuery({
    variables: {
      id: objId
    }
  })

  // 
  const navigate = useNavigate();

  // ownerIduserId
  const globalUser = useRecoilValue(GlobalUser);

  const titleRef = useRef<HTMLInputElement>(null);
  const descRef = useRef<HTMLTextAreaElement>(null);

  // 
  const [errorMessage, setErrorMessage] = useState<Error>();

  // , , 
  const { upload, loading, error: uploadError } = useModelUpdate();

  // 
  const submit = () => {
    setErrorMessage(undefined);

    if (!globalUser?.id)
      return setErrorMessage(new Error(''));
    if (!modelFile || !thumbFile)
      return setErrorMessage(new Error(''));
    if (!titleRef.current?.value)
      return setErrorMessage(new Error(''));

    upload({
      modelId: currentModel?.models_by_pk?.id!,
      file: {
        model: modelFile,
        thumbnail: thumbFile,
      },
      title: titleRef.current.value,
      description: descRef.current?.value,
    }).then((data) => {
      // `/detail/modelId` 
      if (data?.id) {
        navigate(`/detail/${objId}`);
      }
    });
  };

  // 
  useEffect(() => {
    setErrorMessage(uploadError);
  }, [uploadError]);

  return (
    <>
      <label className={styles.label}>
        <Typography variant='body2'></Typography>
        <TextField
          size='small'
          fullWidth
          variant='outlined'
          inputRef={titleRef}
          defaultValue={currentModel?.models_by_pk?.title}
        />
      </label>

      <label className={styles.label}>
        <Typography variant='body2'></Typography>
        <TextField
          size='small'
          fullWidth
          variant='outlined'
          multiline
          rows={4}
          inputRef={descRef}
          defaultValue={currentModel?.models_by_pk?.description}
        />
      </label>

      {
        // 
        errorMessage?.message && (
          <label className={styles.label}>
            <Typography color='error'>{errorMessage.message}</Typography>
          </label>
        )
      }

      <div className={styles.button}>
        <Button
          variant='contained'
          color='primary'
          disabled={loading}
          onClick={submit}
        >
          {loading ? '' : ''}
        </Button>
      </div>
    </>
  );
};
