import { Card, CardContent, CardHeader, Divider, Typography, Button } from "@material-ui/core";
import AddCircleIcon from '@material-ui/icons/AddCircle';
import CancelIcon from '@material-ui/icons/Cancel';
import { useEffect, useState, Suspense } from "react";
import { Link } from "react-router-dom";

import { Three } from "../../../components/Three"
import useStyles from "./style";

export type CanvasAreaProps = {
  modelId: string | undefined;
  title: string | undefined;
  created: Date | undefined;
  owner: string | undefined;
  avatar: React.ReactNode | undefined;
  description: string | undefined;
  views: number | undefined;
  subscribers: number | undefined;  // 
  isCurrentModelByOthers: boolean;  // 
  isCurrentModelByMine: boolean;  // 
  isSubscribed: boolean;  // 
  onSubscribe: () => any;  // 
  onUnSubscribe: () => any;  // 
  fetcher: () => Promise<string | undefined>;
};

export const CanvasArea = ({
  modelId,
  title,
  created,
  owner,
  avatar,
  description,
  views,
  subscribers,
  isCurrentModelByOthers,
  isCurrentModelByMine,
  isSubscribed,
  onSubscribe,
  onUnSubscribe,
  fetcher
}: CanvasAreaProps) => {
  const styles = useStyles();
  // URL
  const [src, setSrc] = useState<string>();
  useEffect(() => {
    // Firebase Storage
    fetcher().then(setSrc);
  });

  return (
    <Card>
      {/* 3D */}
      <CardContent className={styles.canvas} >
        <Suspense fallback={<div style={{ color: "white", textAlign: "center", marginTop: 100 }}>Now Loading...</div>}>
          {src ? (
            <>
              <Three glbSrc={src} />
            </>
          ) : (
            <></>
          )}
        </Suspense>
      </CardContent>

      {/*  */}
      <CardContent>
        <Typography component="h2" variant="h4">{title}</Typography>
        <Typography variant="body2" color="textSecondary">{created ? new Date(created).toLocaleDateString() : ""}</Typography>
        <Typography variant="body2" color="textSecondary">{views}</Typography>
      </CardContent>

      {/*  */}
      <Divider />

      {/*  */}
      <CardHeader
        avatar={avatar}
        title={owner}
        subheader={`${subscribers || 0}`}
      />

      {/* or */}
      {isCurrentModelByOthers &&
        <div className={styles.channelBtn}>
          {isSubscribed ? (
            <Button
              variant="contained"
              color="default"
              onClick={onUnSubscribe}
              startIcon={<CancelIcon />}
            >
              
            </Button>
          ) : (
            <Button
              variant="contained"
              color="primary"
              onClick={onSubscribe}
              startIcon={<AddCircleIcon />}
            >
              
            </Button>
          )}
        </div>
      }

      {/*  */}
      <CardContent className={styles.descPadding}>
        <Typography>{description}</Typography>
      </CardContent>

      {/*  */}
      { isCurrentModelByMine &&
        <Link to={`/detail/${modelId}/update`} style={{ textDecoration: "none" }}>
          <Button
            variant="contained"
            color="primary"
            style={{ margin: 50 }}
          >
            
          </Button>
        </Link>
      }
    </Card>
  );
};