import { atom } from "recoil";
import { Subscribers, Users } from "../../utils/graphql/generated";

// PickTypescript
export type GlobalUserType =
  | Pick<
      Users,
      | "id"
      | "name"
      | "email"
      | "profile_photo_url"
      | "created_at"
      | "updated_at"
    >
    & { subscribersByUserid?: Pick<Subscribers, "subscribe_id">[] }
  | undefined;

// key
export const GlobalUser = atom<GlobalUserType>({
  key: "GlobalUser",
  default: undefined,
});