import { useEffect, useState } from "react";
import { useRecoilValue } from "recoil";

import { storage } from "../../utils/Firebase/config";
import { UserByIdDocument, useUpdateUserMutation } from "../../utils/graphql/generated";
import { GlobalUser } from "../../stores/User";

type UploadProps = {
  file: {
    avatar: File | undefined;
  };
  name: string;
  description?: string;
  userId: string;
}

export const useAvatarUpload = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<Error>();

  const [mutation, { error: apolloError }] = useUpdateUserMutation({
    refetchQueries: [{ query: UserByIdDocument }],
  });

  // modelownerIduserid
  const globalUser = useRecoilValue(GlobalUser);

  // Firebase Storage
  const uploadStorage = (id: string, file: File | undefined, path: string) => {
    // 
    if (file) {
      const exe = file.name.split('.').pop();
      return storage.ref(`${path}/${id}.${exe}`).put(file);
    } else {
      return null;
    }
  };

  const upload = async({ file, name, description, userId }: UploadProps) => {
    // 
    if (!globalUser?.id) return;

    // 
    setLoading(true);
    // avatarID
    const avatarName = globalUser.id;

    // try-catchPromise()
    try {
      // avatar
      const avatarUploadTask = await uploadStorage(avatarName, file.avatar, "avatars");

      // avatar URL
      let avatarURL: string = "";
      if (avatarUploadTask) {
        avatarURL = await avatarUploadTask.ref.getDownloadURL();
      } else {
        avatarURL = globalUser.profile_photo_url || "";
      }

      // avatar
      const res = await mutation({
        variables: {
          id: userId,
          name: name,
          profile_photo_url: avatarURL,
        }
      });

      // avatar
      return res.data?.update_users_by_pk;
    } catch(err) {
      // 
      console.error(err);
      setError(new Error(""));
    } finally {
      // false
      setLoading(false);
    }
  };

  // Appollo Client
  useEffect(() => {
    if (apolloError) {
      console.error(apolloError);
      setError(new Error(""));
    }
  }, [apolloError])

  return {
    loading,
    error,
    upload
  };
};